import "./forme.css";
const Forme = () => {
  return (
    <div className="forme body d-flex flex-column min-vh-100 ">
      <div className="row row-cols-1 row-cols-md-3 g-4 m-3">
        <div className="col">
          <div className="card  mb-3 mt-5 mx-2">
            <div className="card-header">Police de caractres et styles</div>
            <div className="card-body ">
              <div className="card-text">
                <ul>
                  <li>
                    Utiliser la police "Times New Roman" en taille de 12 points
                    et un interligne de 1,5 cm.
                  </li>
                  <li>
                    Justifier le texte et viter l'utilisation du soulignement,
                    du gras et de l'italique.
                  </li>
                  <li>
                    Appliquer la mme police et la mme taille de caractres aux
                    titres.
                  </li>
                  <li>
                    Utiliser les "Styles" pour les diffrents niveaux de titres.
                  </li>
                  <li>
                    Vrifier la numrotation et tre attentif aux titres
                    orphelins.
                  </li>
                  <li>viter l'utilisation des chiffres romains.</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div className="col">
          <div className="card  mb-3 mt-5 mx-2">
            <div className="card-header">Pagination</div>
            <div className="card-body ">
              <div className="card-text">
                <ul>
                  <li>
                    Indiquer le numro des pages dans le coin infrieur droit
                    des feuilles, sans utiliser de point, de tiret ni de
                    parenthses.
                  </li>
                  <li>
                    Numroter la page de l'introduction gnrale comme page 1.
                  </li>
                  <li>
                    {" "}
                    Paginer toutes les pages, de l'introduction gnrale jusqu'
                    la fin du rapport, en utilisant des chiffres arabes(1, 2, 3,
                    etc.) <strong> y compris les annexes.</strong>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        <div className="col">
          <div className="card  mb-3 mt-5 mx-2">
            <div className="card-header">Ponctuation</div>
            <div className="card-body ">
              <div className="card-text">
                <ul>
                  <li>
                    {" "}
                    Placer une espace aprs les signes simples, tels que le
                    point, le point-virgule et les trois points de suspension.
                  </li>
                  <li>
                    Insrer une espace avant et une espace aprs les signes
                    doubles, tels que les deux points, le point-virgule, le
                    point d'exclamation, le point d'interrogation, les
                    guillemets et les parenthses.
                  </li>
                  <li>
                    Ne pas insrer d'espace  l'intrieur des crochets, des
                    guillemets et des parenthses.
                  </li>
                  <li>
                    Ne pas ajouter d'espace avant ni aprs le trait d'union et
                    l'apostrophe. Cependant, ajouter une espace aprs le tiret
                    s'il est utilis pour une numration.
                  </li>
                  <li>
                    Ne pas suivre "etc." d'un point de suspension. Utiliser soit
                    trois points de suspension, soit "etc.", mais pas les deux.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div className="col text-black">empty</div>
        <div className="col">
          <div className="card  mb-3 mt-5 mx-2">
            <div className="card-header">Marge</div>
            <div className="card-body ">
              <div className="card-text">
                <ul>
                  <li>
                    Dfinir une marge de 2 cm de chaque ct{" "}
                    <strong>avec 0.5cm ou 1 cm de plus du ct gauche</strong>{" "}
                  </li>
                  <li>
                    {" "}
                    Positionner l'entte et le pied de page  une distance de
                    1,5 cm  partir du bord.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Forme;
